<template>
  <div class='root'>
    <action-bar title='订单管理' :left="false" :border="false"></action-bar>

    <div class="type-root">
      <div class="order-type">
        <div v-for="(item, index) in orderType" :key="index" class="order-type-child"
          @click="bindSwtichOrderType(item.type, index)">
          <div :class="['order-type-ab', orderType.length > 1 ? 'order-type-ab-center' : '']">
            <div :class="typeCurrent == index ? 'ar' : ''">
              <div :class="typeCurrent == index ? 'type-active-left' : ''"></div>
            </div>
            <div
              :class="[typeCurrent == index ? 'type-active' : '', orderType.length < 3 ? 'type-active-padding' : '']">
              {{ item.name }}</div>
            <div :class="typeCurrent == index ? 'ar' : ''">
              <div :class="typeCurrent == index ? 'type-active-right' : ''"></div>
            </div>
          </div>
        </div>
      </div>
      <div class="order-screen" @click="bindScreen">
        <div>筛选</div>
        <div class="triangle"></div>
      </div>
    </div>
    <div class='container'>
      <div class='content'>
        <van-tabs background="#f2f2f2" class="tabs" @change="swiperTab" :ellipsis="false" v-model="orderStateCurrent"
          animated swipeable color="#42D18A">
          <van-tab :title="pageItem.title" v-for="(pageItem, pageIndex) in orderTypeData[typeCurrent].tablist"
            :key="pageItem.title">
            <scroll-view :ref="`scroll${pageIndex}`" refresher loadmore @pullrefresh="onRefresh"
              @scrolltolower="onLoadMore" :data="orderList" :noMoreData="noMoreData">
              <div class="sc-top"></div>
              <div class="card" v-for="(item, index) in orderList" :key="item.orderId">
                <div v-if="type != ''" class="order-item">
                  <div class="goods-top">
                    <div>{{ item.phone }}</div>
                    <div>{{ item.state }}</div>
                  </div>
                  <goods-item v-for="(goodsItem, goodsIndex) in item.orderItems" :key="goodsIndex">
                    <van-image slot="imgSlot" radius=".25rem" :src="baseUrl + goodsItem.imageUrl" class="goods-img" />
                    <div slot="nameSlot" class="goods-name">{{ goodsItem.productName }}</div>
                    <div slot="attributeSlot">
                      <div class="attribute">
                        <div></div>
                        <div class="attribute-content">x{{ goodsItem.count }}</div>
                      </div>
                      <div class="attribute">
                        <div class="attribute-content">颜色:{{ goodsItem.color }} 级别:{{ goodsItem.level }} 规格:{{
                          goodsItem.unit
                        }}
                        </div>
                        <div>¥ {{ goodsItem.unitPrice }}</div>
                      </div>
                    </div>
                  </goods-item>

                  <div class="item-price">
                    <div class="item-price-content">
                      <span class="item-price-money">优惠金额</span><span>¥ {{ item.raisePriceAll }}</span>
                    </div>
                    <div class="item-divider"></div>
                    <div class="item-price-content">
                      <span class="item-price-money">实付</span><span>¥ {{ item.consumePrice }}</span>
                    </div>
                  </div>

                  <!-- 配送方式 -->
                  <div class="item-other">
                    <div class="item-news">
                      <span class="item-traffic">订单备注</span><span class="item-traffic-remark">{{ item.remark }}</span>
                    </div>
                    <div class="item-news">
                      <span class="item-traffic">配送方式</span>{{ item.typeOfTraffic }}
                    </div>
                    <div class="item-news">
                      <span class="item-traffic">订单编号</span>{{ item.alias }}
                    </div>
                    <div class="item-bottom">
                      <span class="item-traffic">下单时间</span>{{ item.createDateTime }}
                    </div>
                    <div @click="bindPrepare(index)" v-if="item.distributionBtn && item.distributionBtn != ''"
                      :class="['item-dis-position', 'item-prepare', item.distributionState == '0' ? 'prepared' : 'not-prepare']">
                      <!-- {{item.distributionBtn}} -->
                      {{ item.distributionState == '0' ? '备货' : '备货中' }}
                    </div>
                    <!-- 已配货印章 -->
                    <span v-if="item.distributionState == '1'" class="item-prepared">备货中</span>
                  </div>
                </div>
              </div>
            </scroll-view>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>
<script>

import { Tab, Tabs } from 'vant'
import { distribution, orderLists } from '@/http/api'
import goodsItem from '@/components/util/GoodsItem.vue'
import ScrollView from '@/components/util/ScrollView.vue'

export default {
  name: 'order',
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    goodsItem,
    ScrollView
  },
  data () {
    return {
      orderType: [{
        name: '现货订单',
        type: '普通'
      }, {
        name: '预售订单',
        type: '预定'
      }, {
        name: '维权订单',
        type: ''
      }], // 订单类型
      typeCurrent: 0, // 选项卡选中下标
      orderTypeData: [{
        tablist: [{
          title: '全部',
          value: '全部'
        }, {
          title: '未支付',
          value: '未支付'
        }, {
          title: '未发货',
          value: '未发货'
        }, {
          title: '已发货',
          value: '已发货'
        }, {
          title: '已完成',
          value: '已完成'
        }, {
          title: '已取消',
          value: '已取消'
        }]
      }, {
        tablist: [{
          title: '全部',
          value: '全部'
        }, {
          title: '未支付',
          value: '未支付'
        }, {
          title: '未发货',
          value: '未发货'
        }, {
          title: '已发货',
          value: '已发货'
        }, {
          title: '已完成',
          value: '已完成'
        }, {
          title: '已取消',
          value: '已取消'
        }]
      }, {
        tablist: [{
          title: '全部',
          value: '全部'
        }, {
          title: '待处理售后',
          value: '待处理售后'
        }, {
          title: '同意售后',
          value: '确认售后'
        }, {
          title: '售后申诉',
          value: '申诉'
        }]
      }], // 选项卡内tab
      orderStateCurrent: 0, // 订单状态选中下标
      orderList: [], // 列表数据
      noMoreData: false, // 没有更多数据了
      isReset: true, // 是否重置页面
      state: '全部', // 单据状态
      type: '', // 单据类型 （普通/预定）
      date: '',
      goodsStockNo: '',
      orderAlias: '',
      typeOfTraffic: '',
      rId: '',
      start: 0,
      limit: 10,
      showOperation: false

    }
  },

  beforeRouteEnter (to, from, next) {
    next()
    const orderScreen = from.params.orderScreen
    if (orderScreen) {
      // console.log('orderScreen', orderScreen)
      next(vm => {
        // this.date
        vm.goodsStockNo = orderScreen.goodsStockNo
        vm.orderAlias = orderScreen.orderAlias
        vm.typeOfTraffic = orderScreen.typeOfTraffic
        vm.rId = orderScreen.rId
        vm.start = 0
        vm.isReset = true
        vm.requestList()
      })
    }
  },
  methods: {
    // 筛选
    bindScreen () {
      this.$router.push('/orderScreen')
    },
    // 上拉加载更多
    onLoadMore () {
      if (this.noMoreData) {
        return
      }
      const start = this.start + this.limit
      this.start = start
      this.requestList()
    },
    onRefresh () {
      this.isReset = true
      this.start = 0
      this.requestList()
    },
    // 配货
    bindPrepare (index) {
      const orderList = this.orderList
      const order = orderList[index]
      let state = order.distributionState
      if (state === '1' || state === 1) {
        state = '0'
      } else {
        state = '1'
      }
      distribution({
        state,
        orderId: order.orderId
      }).then(res => {
        console.log(res.data.state)
        if (res.data.state !== undefined) {
          order.distributionState = res.data.state
          order.distributionBtn = res.data.distributionBtn
          this.orderList = orderList
        }
      }).catch(res => {
        this.$toast(res.msg)
      })
    },
    // 切换订单类型(现货、预售、维权)
    bindSwtichOrderType (type, index) {
      this.typeCurrent = index
      this.orderStateCurrent = 0
      this.type = type
      this.start = 0
      this.orderList = []
      this.isReset = true
      this.requestList()
    },
    requestList () {
      if (this.type === '') {
        // this.getAftersalesList()
        this.getOrderList()
      } else {
        this.getOrderList()
      }
    },
    // 获取(普通、预售)订单
    getOrderList () {
      const data = {
        state: this.state,
        type: this.type,
        date: this.date,
        goodsStockNo: this.goodsStockNo,
        orderAlias: this.orderAlias,
        typeOfTraffic: this.typeOfTraffic,
        rId: this.rId,
        start: this.start,
        limit: this.limit
      }
      orderLists(data).then(res => {
        this.scrollFinally()
        this.pushListData(res.data.orderList)
      }).catch(res => {
        this.scrollFinally()
        this.$toast(res.msg)
      })
    },
    scrollFinally () {
      const scroll = this.$refs[`scroll${this.orderStateCurrent}`][0]
      scroll.refreshed()
      scroll.loaded()
    },
    // 填充数据
    pushListData (list) {
      let nmd = false
      if (list.length < 10) {
        nmd = true
      }
      let orderList = this.orderList
      if (orderList == null || this.isReset === true) {
        orderList = list
      } else {
        orderList.push(...list)
      }
      this.orderList = orderList
      this.noMoreData = nmd
      this.isReset = false
    },
    // 滑动切换
    swiperTab (index) {
      const state = this.orderTypeData[this.typeCurrent].tablist[index].value
      this.state = state
      this.orderStateCurrent = index
      this.start = 0
      this.orderList = []
      this.isReset = true
      this.requestList()
    }
  },
  mounted () {
    this.type = this.orderType.length > 0 ? this.orderType[this.typeCurrent].type : ''
    this.requestList()
  }
}
</script>
<style lang="scss" scoped>
@import '@/utils/tabScroll.css';

.root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.container {
  flex-grow: 1;
  position: relative;
}

.content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #f2f2f2;
  padding-bottom: 3.125rem;
  box-sizing: border-box;
}

.type-root {
  display: flex;
  flex-direction: row;
  background-color: white;
}

.order-type {
  display: flex;
  flex-grow: 1;
}

.order-type-child {
  flex-grow: 1;
  text-align: center;
  color: #333;
  height: 2.125rem;
  line-height: 2.125rem;
  position: relative;
  white-space: nowrap;
  font-size: .875rem;
}

.order-type-ab {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.order-type-ab-center {
  justify-content: center;
}

.ar {
  position: relative;
  background-color: #f2f2f2;
  width: .625rem;
  height: 2.125rem;
}

.type-active {
  font-weight: bolder;
  background-color: #f2f2f2;
  border-radius: .625rem .625rem 0 0;
  padding: 0 .625rem;
}

.type-active-padding {
  padding: 0 1.875rem;
}

.type-active-left {
  width: .625rem;
  height: 2.125rem;
  background-color: white;
  border-radius: 0 0 .625rem 0;
  position: absolute;
}

.type-active-right {
  width: .625rem;
  height: 2.125rem;
  background-color: white;
  border-radius: 0 0 0 .625rem;
  position: absolute;
}

.order-screen {
  display: flex;
  flex-direction: row;
  padding: 0 .6875rem;
  align-items: center;
  margin: 0 .6875rem;
  height: 2.125rem;
  font-size: .875rem;
}

.triangle {
  width: 0;
  height: 0;
  border-right: .3125rem solid transparent;
  border-left: .3125rem solid transparent;
  border-top: .3125rem solid #515151;
  margin-left: .5625rem;
}

.tabs {
  width: 100%;
  height: 100%;
}

.van-tabs {
  background-color: #f2f2f2;
}

.order-item {
  background-color: white;
  margin: 0 .5rem;
  padding: .5rem;
  border-radius: .375rem;
}

.sc-top {
  height: .5rem;
}

.card {
  padding-bottom: .5rem;
}

.goods-top {
  color: #42D18A;
  font-size: 1rem;
  display: flex;
  justify-content: space-between;
  margin-bottom: .375rem;
  margin-left: .1875rem;
  margin-right: .1875rem;
}

.goods-img {
  width: 22vw;
  height: 22vw;
  background-color: #F2F2F2;
  margin-right: .375rem;
}

.goods-name {
  font-size: .875rem;
}

.attribute {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: .75rem;
  color: #333;
  margin-top: .3125rem;
}

.attribute-content {
  color: #999;
  font-size: .75rem;
}

.item-price {
  margin: .8125rem 0;
  background-color: #f5f7f9;
  border-radius: .375rem;
  padding: .5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.item-price-content {
  flex: 1 1 0;
  color: #333;
  font-size: .875rem;
  text-align: center;
}

.item-price-money {
  color: #999;
  margin-right: .6875rem;
  font-size: .875rem;
}

.item-other {
  position: relative;
  font-size: .875rem;
}

.item-news {
  margin: .5625rem 0;
}

.item-traffic {
  margin-right: .6875rem;
  color: #999999;
}

.item-bottom {
  margin-top: .5625rem;
}

.item-dis-position {
  position: absolute;
  bottom: 0;
  right: 0;
}

.item-prepare {
  width: 4.6875rem;
  height: 1.875rem;
  text-align: center;
  border-radius: .375rem;
  box-sizing: border-box;
}

.prepared {
  color: white;
  background-color: #42D18A;
  line-height: 1.875rem;
}

.not-prepare {
  line-height: 1.8125rem;
  color: #42D18A;
  border: .0625rem solid #42D18A;
  background-color: white;
}

.item-prepared {
  font-size: 1.125rem;
  color: red;
  padding: .25rem;
  border: .0625rem dashed red;
  position: absolute;
  bottom: 1.875rem;
  right: 6.25rem;
  transform: rotate(20deg);
}
</style>
